// Small tablets and large smartphones (landscape view)
$screen-sm-min: 576px;

// Small tablets (portrait view)
$screen-md-min: 768px;

// Tablets and small desktops
$screen-lg-min: 992px;

// Large tablets and desktops
$screen-xl-min: 1200px;

// Small devices
@mixin sm {
  @media (min-width: #{$screen-sm-min}) {
    @content;
  }
}

// Medium devices
@mixin md {
  @media (min-width: #{$screen-md-min}) {
    @content;
  }
}

// Large devices
@mixin lg {
  @media (min-width: #{$screen-lg-min}) {
    @content;
  }
}

// Extra large devices
@mixin xl {
  @media (min-width: #{$screen-xl-min}) {
    @content;
  }
}

.App {
  text-align: center;
}
body {
  @include sm {
    background-color: rgb(255, 255, 255);
  }
  @include md {
    background-color: rgb(0, 111, 223);
  }
  @include lg {
    background-color: rgb(236, 0, 0);
  }
  @include xl {
    background-color: rgb(42, 43, 44);
  }
}

.container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.gif-item {
  margin: 20px;
  @include sm {
    flex: 1 0 90%;
  }
  @include md {
    flex: 1 0 40%;
  }
  @include lg {
    flex: 1 0 25%;
  }
  @include xl {
    flex: 1 0 21%;
  }
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
